import React, { useEffect, useState } from 'react';
import { Select, Space, Table, Tag } from 'antd';
import type { TableProps } from 'antd';

import myData from '../page/schedule.json';
import 'dayjs'
import dayjs from 'dayjs';
import customParseFormat from "dayjs/plugin/customParseFormat";
import isBetween from 'dayjs/plugin/isBetween'

dayjs.extend(customParseFormat)
dayjs.extend(isBetween)
interface Day {
    day: string,
    lessons: Lesson[]
}

interface Lesson {
    key: string;
    subject: string;
    class: string;
    room: string;
    time: string;
    note: string[];
}






const lessonTimes = new Map<string, string>([
    ["1-1", "9.00-9.35"],
    ["1-2", "10.00-10.35"],
    ["1-3", "11.00-11.35"],
    ["1-4", "12.05-12.40"],
    ["1-5", "13.05-13.40"],
    ["2-1", "9.00-9.45"],
    ["2-2", "10.00-10.45"],
    ["2-3", "11.00-11.45"],
    ["2-4", "12.05-12.50"],
    ["2-5", "13.05-13.50"],
    ["2-6", "14.05-14.50"],
    ["2-7", "15.00-15.45"],
])

const rooms = new Map<string, string>([
    ["1А", "207"],
    ["1Б", "203"],
    ["1В", "208"],
    ["1Г", "209"],
    ["1Д", "202"],
    ["2А", "401"],
    ["2Б", "400"],
    ["2В", "307"],
    ["2Г", "309"],
    ["2Д", "308"],
    ["3А", "402"],
    ["3Б", "405"],
    ["3В", "104"],
    ["3Г", "404"],
    ["3Д", "403"],
    ["4А", "302"],
    ["4Б", "101"],
    ["4В", "103"],
    ["4Г", "303"],
    ["4Д", "102"]
])

const dayOfWeek = [
    {
        value: 'Понедельник',
        label: 'Понедельник',
    },
    {
        value: 'Вторник',
        label: 'Вторник',
    },
    {
        value: 'Среда',
        label: 'Среда',
    },
    {
        value: 'Четверг',
        label: 'Четверг',
    },
    {
        value: 'Пятница',
        label: 'Пятница',
    },
    {
        value: 'Суббота',
        label: 'Суббота',
    },
    {
        value: 'Неделя',
        label: 'Неделя',
    },
]

const dayMap = new Map<number, string>([
    [1, "Понедельник"],
    [2, "Вторник"],
    [3, "Среда"],
    [4, "Четверг"],
    [5, "Пятница"],
    [6, "Суббота"],
    [7, "Неделя"],
])

function convertCurrentDay(): string {

    let day = new Date()

    day.getDay()

    return dayMap.get(day.getDay())!

}

const selectStyle: React.CSSProperties = {
    margin: "10px 45%",
    height: "40px"

}

const tableStyles: React.CSSProperties = {
    margin: "10px 5%",
    padding: "10px",
    boxShadow: "3px 7px 6px 7px rgba(54, 86, 111, 0.31)",
    borderRadius: "10px"

}

function TableSchedule() {


    const [selectedDay, setSelectedDay] = useState<string>(convertCurrentDay())

    useEffect(() => {
        takeData()
    })

    const columns: TableProps<Lesson>['columns'] = [
        {
            title: 'Предмет',
            dataIndex: 'subject',
            key: 'subject',
            render: (text: string) => <p>{text}</p>,
        },
        {
            title: 'Класс',
            dataIndex: 'class',
            key: 'class',
        },
        {
            title: 'Кабинет',
            dataIndex: 'room',
            key: 'room',
            render: (_, record) => <p>{rooms.get(record.class)}</p>,
        },
    
        {
            title: 'Время',
            dataIndex: 'room',
            key: 'room',
            render: (_, record) => <p>{lessonTimes.get(record.time)}</p>,
        },
        {
            title: "Статус",
            render: (_, record) =>
                <>
                    <p>{takeStatus(record)}</p>
                </>
        }
    ];
    

    const takeStatus = (lessons : Lesson) =>{
        let times = lessonTimes.get(lessons.time)!.replaceAll(".", ":").split("-")
        let startTime = times[0]
        let endTime = times[1]
    
        let st = dayjs(startTime, "H:mm")
        let et = dayjs(endTime, "H:mm")
        let now = dayjs(new Date())
        
        if(now.isAfter(et)){
            return <Tag color = 'red'> Прошёл</Tag>
        }
        else if(now.isBefore(et) && now.isAfter(st)){
            return <Tag color = 'yellow'> Сейчас</Tag>
        }
        return  <Tag color = 'green'> Будет</Tag>
        
    }

    const onChange = (value: string) => {
        setSelectedDay(value)
    };



    const takeData = () => {

        let schedules = myData.schedule as Day[]
        return schedules.find(e => e.day.toLowerCase() === selectedDay.toLowerCase())!.lessons
    }


    return (
        <>
            <Select
                style={selectStyle}
                placeholder="Выберите день недели"
                optionFilterProp="label"
                defaultValue={convertCurrentDay()}
                onChange={onChange}
                options={dayOfWeek}
            />
            <Table
                style={tableStyles}
                columns={columns}
                dataSource={takeData()}
                pagination={false} 
                
                />
        </>
    )
}




export default TableSchedule;